<template>
  <div id="app">
    <div class="header">
      <footer class="link_cal">
        <router-link :to="{ name: 'main' }">
<!--          <img class="link_cal" :src="require('@/assets/back.png')" alt="Strategy">-->
        </router-link>
      </footer>
    </div>

    <div class="container">
      <div class="calculator">
        <h2>Калькулятор стратегии</h2>
        <div class="form">
          <label for="entity">Выберите персонажа или оружие:</label>
          <select v-model="selectedEntity" id="entity">
            <option v-for="entity in entities" :key="entity.id" :value="entity.name">
              {{ entity.name }}
            </option>
          </select>

          <div v-if="selectedEntity">
            <label for="newLevel">Желаемый уровень:</label>
            <input type="number" v-model.number="newLevel" id="newLevel" min="1" max="90" />

            <button @click="fetchStrategy">Рассчитать стратегию</button>
          </div>
        </div>
      </div>

      <div v-if="strategy.points.length > 0" class="strategy-table">
        <h3>Результаты стратегии</h3>
        <table>
          <thead>
          <tr>
            <th>Дата</th>
            <th>Данж</th>
            <th>Материал</th>
            <th>Сколько сходить</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(entry, index) in strategy.points" :key="index">
            <td>{{ entry.date }}</td>
            <td>{{ entry.dungeonName }}</td>
            <td>{{ entry.itemName }}</td>
            <td>{{ entry.tryingCount }}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      entities: [
        { id: 1, name: 'Amber'},
        { id: 2, name: 'Barbara'},
        { id: 3, name: 'Beidou'},
        { id: 4, name: 'Bennett'},
        { id: 5, name: 'Chongyun'},
        { id: 6, name: 'Diluc'},
        { id: 7, name: 'Diona'},
        { id: 8, name: 'Eula'},
        { id: 9, name: 'Fischl'},
        { id: 10, name: 'Ganyu'},
        { id: 11, name: 'Hu Tao'},
        { id: 12, name: 'Jean'},
        { id: 13, name: 'Kaeya'},
        { id: 14, name: 'Klee'},
        { id: 15, name: 'Lisa'},
        { id: 16, name: 'Mona'},
        { id: 17, name: 'Ningguang'},
        { id: 18, name: 'Noelle'},
        { id: 19, name: 'Qiqi'},
        { id: 20, name: 'Razor'},
        { id: 21, name: 'The Precious Pool'},
        { id: 22, name: 'Heavenly majesty'},
        { id: 23, name: 'The Gracious Lord of Waters'},
        { id: 24, name: 'Desecrated desire'},
        { id: 25, name: 'The memory of dust'},
        { id: 26, name: 'Montenegrin agate'},
        { id: 27, name: 'Deadly battle'},
        { id: 28, name: 'Aqua Simulacrum'},
        { id: 29, name: 'Desiccant'},
        { id: 30, name: 'The Heavenly Sword'}
      ],
      selectedEntity: null,
      newLevel: 1,
      strategy: {
        points: [],
      },
    };
  },
  methods: {
    async fetchStrategy() {
      try {
        const response = await fetch(`/strategy?name=${this.selectedEntity}&newLevel=${this.newLevel}`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
          },
        });

        if (!response.ok) {
          throw new Error("Ошибка при получении данных");
        }

        const data = await response.json();
        this.strategy = data;
      } catch (error) {
        console.error("Не удалось получить стратегию:", error);
      }
    },
  },
};
</script>


<style>
.calculator {
  height: auto; /* Устанавливается автоматически по содержимому */
  max-height: 500px; /* Максимальная высота */
  overflow: hidden; /* Скрывать излишки содержимого */
  margin: 20px auto;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 1000;
  width: 50%;
  min-width: 300px;
  margin-top: 150px;
  margin-left: 20px;
}

.form label {
  display: block;
  margin: 10px 0 5px;
}

.form input, .form select {
  margin-bottom: 15px;
  padding: 5px;
  width: 100%;
}

.strategy-table {
  z-index: 1000;
  flex: 1;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  width: 50%; /* Ширина 50% от родителя */
  min-width: 300px; /* Минимальная ширина */
  margin-top: 150px; /* Отступ сверху */
  margin-left: 20px;
}

.strategy-table table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.strategy-table th, .strategy-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

button {
  background-color: #12e1d0; /* Зеленый фон */
  color: white; /* Белый текст */
  border: none; /* Убираем границы */
  padding: 10px 20px; /* Пространство внутри кнопки */
  text-align: center; /* Текст по центру */
  text-decoration: none; /* Убираем подчеркивание */
  display: inline-block; /* Для центрирования */
  font-size: 16px; /* Размер шрифта */
  margin: 10px 0; /* Отступ сверху и снизу */
  cursor: pointer; /* Курсор в виде руки */
  border-radius: 5px; /* Скругленные углы */
  transition: background-color 0.3s, transform 0.2s; /* Анимация */
}

button:hover {
  background-color: #12e1d0; /* Более яркий зеленый при наведении */
  transform: scale(1.05); /* Легкое увеличение */
}

button:active {
  background-color: #1e9c92; /* Темно-зеленый при клике */
  transform: scale(0.95); /* Легкое уменьшение */
}

.container {
  display: flex;
  gap: 20px;
}
</style>
